.container {
  width: 420px;
  background-color: rgb(70, 67, 67);
  margin: auto;
  text-align: center;
  padding: 11px;
  border: 10px;
  margin-top: 55px;
}
.container input {
  width: 90%;
  padding: 11px;
  border: none;
  font-size: 20px;
  margin-top: 11px;
  margin-top: 11px;
  border-radius: 3px;
}
.container button {
  width: 90px;
  padding: 7px;
  margin: 5px;
  border: none;
  font-size: 20px;
  background-color: #d1d1d1;
  border-radius: 4px;
  cursor: pointer;
}
.container .equal {
  background-color: rgb(0, 162, 255);
  color: white;
}
